  /* For desktop: */



.accordion{
    max-height: 550px;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
}
.accordion::-webkit-scrollbar-track
{
	-webkit-box-shadow: inset 0 0 6px rgba(44, 43, 43, 0.158);
	background-color: #f5f5f53a;
}

.accordion::-webkit-scrollbar
{
	width: 10px;
	background-color: #f5f5f521;
}

.accordion::-webkit-scrollbar-thumb
{
	background-color: #413f3f23;
	border: 2px solid #fff9f92c;
}